import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import { connect } from "redux-zero/react";
import { Carousel } from 'react-bootstrap';
import { Redirect,Switch,Route,NavLink } from 'react-router-dom';
import {Header} from './About';

const ItemImg = ({item,index}) => {
    return(
          <div className="col-lg-3 col-md-3 col-sm-4 col-xs-4 transparent-skills">
              <div className="container-information">
                    <div className="cont-i"><i className={item.img} id="i"/></div>         
                </div> 
            </div>

    )
  
  }
  const Skills =({images})=>{
    return(
      <div className="row" id="habilidades">
          <div className="container skills">
          <div className="col-md-12 text-center text-skills">
          <h3>Habilidades</h3>
          <p>Estas son mis habilidades técnicas</p>
          </div>
          <div className ="col-lg-12 col-md-12 col-xs-12">
          {
          images.map((item,index)=>{
            return <ItemImg key ={index} item={item} index={index}/>
          })
          }
          </div>
          </div>			
      </div>
      )
  }
  

const mapToProps = ({ images,imgProject }) => ({ images,imgProject});
export default connect(mapToProps)(Skills);


