import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import { connect } from "redux-zero/react";
import { Carousel } from 'react-bootstrap';
import { Redirect,Switch,Route,NavLink } from 'react-router-dom';

export const Header =()=>{
    return(
        <nav class="navbar navbar-default navbar-fixed-top">
        <div class="container">
          <div class="navbar-header">
              <a href="#"><img className="img-reponsive lol"src="img/brand3.png"/></a>
              <button type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target=".collapsible-menu" aria-expanded="false">
                <span class="sr-only">Toggle navigation</span>
                <span class="icon-bar"></span>
                <span class="icon-bar"></span>
                <span class="icon-bar"></span>
              </button>
          </div>
          <div class="collapse navbar-collapse navbar-right collapsible-menu text-center">
          <ul class="nav navbar-nav">
            <li className="hover"><a href="#">Inicio</a></li>
            <li className="hover"><a href="#acerca-de-mi">Acerca de mí</a></li>
            <li className="hover"><a href="#habilidades">Habilidades</a></li>
            <li className="hover"><a href="#proyectos">Proyectos</a></li>
            <li className="hover"><a href="#contacto">Contacto</a></li>
          </ul>
        </div>
        </div>
      </nav>
    )
}

export const About = () =>{
  return (
    <section className="row" id="acerca-de-mi">
    <div className="container about">
        <Header/>
        <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12 details-about">    
            <h3>Acerca de mi</h3>
            <div className="img-about">
            <div className="col-lg-4 col-md-4 col-sm-4  about-img">
                    <img className="img-responsive lol2" src="img/img2.png"/>
                </div>
                <div className=" col-lg-6 col-md-6 col-sm-6 col-xs-12 about-text text-left">
                    <p>
                    Apasionada de la tecnología,con un inmenso amor por la lectura,
                     recopilar información y crear.</p>
                     <p>Autodidacta, curiosa por naturaleza, siempre me enfoco en aprender nuevas herramientas y 
                    estrategias que me ayuden a perfeccionar mis habilidades,
                    </p>
                    <p>Siempre a la vanguardia de nuevas tecnologías que aporten 
                        y mejoren la calidad en los productos que desarrollo, que cumplan con las
                        necesidades del cliente y el usuario para ofrecer una gran
                         experiencia.</p>
                </div>
        </div>
    </div>
    </div>
    </section>
    );

}

export default About;


