import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import { connect } from "redux-zero/react";
import { Carousel } from 'react-bootstrap';
import { Redirect,Switch,Route,NavLink } from 'react-router-dom';
import {Header} from './About';

const Project = ({item,index}) => {
    return(
    <div className="col-lg-4 col-md-4 col-sm-12 col-xs-12 project-div">
          <div>
            <img className="img-responsive space-img" src={item.img} alt="img"/>
          </div>
          <div className="information">
            <h3>{item.name}</h3>
            <span><a href={item.linkgit} target="_blank"><i class="fa fa-github fa-3x" aria-hidden="true"></i></a></span>
            <span><a href={item.link} target="_blank"><i className="fa fa-eye fa-3x" aria-hidden="true"></i></a></span>
          </div>
    </div>
    )
  
  }
  /*

*/
  const Projects =({imgProject })=>{
    return(
      <section className="row" id="proyectos">
        <div className="container projects">
          <div className="col-md-12 text-center projects-cont">
          <h3>Proyectos</h3>
          <p>Aqui esta una muestra de mis trabajos</p>
          </div>
          <div className ="col-lg-12 col-md-12">
          {
          imgProject.map((item,index)=>{
            return <Project key ={index} item={item} index={index}/>
          })
          }
          </div>
        </div>			
      </section>
      )
  }
  

const mapToProps = ({ images,imgProject }) => ({images,imgProject});
export default connect(mapToProps)(Projects);


